// app/components/SpreadIndicator.js
"use client"

import { useEffect, useState } from "react"
import { Line } from "react-chartjs-2"
import { Chart as ChartJS, registerables } from "chart.js"
import { useTheme } from 'next-themes'

ChartJS.register(...registerables)

const SpreadIndicator = ({ orderbookData }) => {
  const [spreadHistory, setSpreadHistory] = useState([])
  const { theme } = useTheme()

  // ... (keep existing useEffect and data processing logic)

  const data = {
    labels: spreadHistory.map((dataPoint) =>
      new Date(dataPoint.timestamp).toLocaleTimeString()
    ),
    datasets: [
      {
        label: "Spread (USD)",
        data: spreadHistory.map((dataPoint) => dataPoint.spread),
        borderColor: theme === 'dark' ? "#3b82f6" : "#2563eb",
        backgroundColor: theme === 'dark' ? "rgba(59, 130, 246, 0.1)" : "rgba(37, 99, 235, 0.1)",
        fill: true,
        tension: 0.1,
        pointRadius: 2,
      },
    ],
  }

  const options = {
    responsive: true,
    plugins: {
      legend: {
        display: true,
        position: "top",
        labels: {
          color: theme === 'dark' ? "#ffffff" : "#000000",
        },
      },
    },
    scales: {
      x: {
        title: {
          display: true,
          text: "Time",
          color: theme === 'dark' ? "#ffffff" : "#000000",
        },
        ticks: {
          color: theme === 'dark' ? "#ffffff" : "#000000",
        },
        grid: {
          color: theme === 'dark' ? "rgba(255, 255, 255, 0.1)" : "rgba(0, 0, 0, 0.1)",
        },
      },
      y: {
        title: {
          display: true,
          text: "Spread",
          color: theme === 'dark' ? "#ffffff" : "#000000",
        },
        ticks: {
          color: theme === 'dark' ? "#ffffff" : "#000000",
        },
        grid: {
          color: theme === 'dark' ? "rgba(255, 255, 255, 0.1)" : "rgba(0, 0, 0, 0.1)",
        },
      },
    },
  }

  return (
    <div className="bg-white dark:bg-gray-800 p-4 rounded-lg shadow-md">
      <h3 className="text-lg font-semibold mb-4 text-gray-800 dark:text-white">Spread Indicator</h3>
      <Line data={data} options={options} />
    </div>
  )
}

export default SpreadIndicator;
